import React, { useContext } from 'react';
import { IconLink } from 'core/src/icons';

import Button from '../Button';
import Group from '../Group';
import { LinkControlProps } from './types';
import { EditorContext } from '../EditorContext';

const AddLinkButton: React.FC<LinkControlProps> = ({ hidden, onClick }) => {
  const { disabled } = useContext(EditorContext);

  return (
    <Group hidden={ hidden }>
      <Button
        onClick={ onClick }
        disabled={ disabled }
        data-test-action="link"
      >
        <IconLink />
      </Button>
    </Group>
  );
};

AddLinkButton.displayName = 'AddLinkButton';

export default React.memo(AddLinkButton);
